  top: 3rem;
  left: 0;
  width: 300px;
  color: #fff;
  overflow-y: hidden;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 1rem;
  overflow-x: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.noticeModal p {
  font-size: 0.6rem;
  position: relative;
}



